.msg-block {
  /*  */
  user-select: text;
}

  .msg-block .msg-content p {
    margin: 0.5rem 0 0.5rem 0;
  }

  .msg-block .msg-content-small p {
    margin: 0.3rem 0 0.2rem 0;
  }

  .msg-block .msg-content ol {
    padding-inline-start: 25px;
  }

  .msg-block .msg-content ul {
    padding-inline-start: 25px;
  }

  .msg-block .msg-content img {
    width: 100%;
    max-width: 40rem;
  }

/* // markdown table */
html[data-theme="light"] .msg-block table {
    background-color: #f8f8f8;
    margin: 0 auto;
    border-collapse: collapse;
    font-size: 1em;
    font-family: Arial, sans-serif;
    line-height: 1.2;
    border: 1px solid #ddd;
  }
html[data-theme="light"] .msg-block table th,
  html[data-theme="light"] .msg-block table td {
    padding: 0.5em 1.2em;
    border: 1px solid #ddd;
    text-align: center;
  }
html[data-theme="light"] .msg-block table th {
    background-color: #e5e5e5;
    font-weight: bold;
    color: #333;
  }
html[data-theme="light"] .msg-block table tr:nth-child(even) {
    background-color: #f2f2f2;
  }
html[data-theme="dark"] .msg-block table {
    background-color: #333;
    color: #fff;
    margin: 0 auto;
    border-collapse: collapse;
    font-size: 1em;
    font-family: Arial, sans-serif;
    line-height: 1.2;
    border: 1px solid #666;
  }
html[data-theme="dark"] .msg-block table th,
  html[data-theme="dark"] .msg-block table td {
    padding: 0.5em 1.2em;
    border: 1px solid #666;
    text-align: center;
  }
html[data-theme="dark"] .msg-block table th {
    background-color: #555;
    font-weight: bold;
  }
html[data-theme="dark"] .msg-block table tr:nth-child(even) {
    background-color: #444;
  }

