/* @tailwind base;
@tailwind components;
@tailwind utilities; */

.title-bar {
  -webkit-app-region: drag;
}

.title-bar .controls {
  -webkit-app-region: no-drag;
}

body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen", "Ubuntu", "Cantarell", "Fira Sans",
    "Droid Sans", "Helvetica Neue", sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  overscroll-behavior: none;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New", monospace;
}

html > body {
  /* font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', Roboto, Helvetica,
        Arial, sans-serif; */
  /* padding: 0; */
  margin: auto;
}

html,
body,
#root {
  /* 100%100vhcalc(var(--vh, 1vh) * 100) */
  height: 100%;
}

html[data-theme="dark"] .ToolBar {
    background-color: var(--chatbox-background-primary);
  }

/* // ------------  --------------- */

/* ,  */

html[data-theme="dark"] ::-webkit-scrollbar {
    width: 6px;
    height: 6px;
    background-color: transparent;
    border-radius: 6px;
  }

html[data-theme="dark"] ::scrollbar {
    width: 6px;
    height: 6px;
    background-color: transparent;
    border-radius: 6px;
  }

html[data-theme="dark"] ::-webkit-scrollbar:hover {
    background-color: #333333;
  }

html[data-theme="dark"] ::scrollbar:hover {
    background-color: #333333;
  }

/*  */

html[data-theme="dark"] ::-webkit-scrollbar-thumb {
    background-color: #b1b1b1;
    border-radius: 6px;
  }

html[data-theme="dark"] ::-webkit-scrollbar-thumb:hover {
    background-color: #8c8c8c;
  }

/*  */

html[data-theme="dark"] ::-webkit-scrollbar-track {
    border-radius: 6px;
  }

/*  */

html[data-theme="dark"] ::-webkit-scrollbar-button {
    display: none;
  }

html[data-theme="dark"] a {
    color: #fff;
  }

/*  */

html[data-theme="dark"] .message-error-tips a {
    color: #2563eb !important;
  }

html[data-theme="light"] .ToolBar {
    background-color: var(--chatbox-background-primary);
  }

/* // ------------  --------------- */

/* ,  */

html[data-theme="light"] ::-webkit-scrollbar {
    width: 6px;
    height: 6px;
    background-color: transparent;
    border-radius: 6px;
  }

html[data-theme="light"] ::scrollbar {
    width: 6px;
    height: 6px;
    background-color: transparent;
    border-radius: 6px;
  }

html[data-theme="light"] ::-webkit-scrollbar:hover {
    background-color: #f5f5f5;
  }

html[data-theme="light"] ::scrollbar:hover {
    background-color: #f5f5f5;
  }

/*  */

html[data-theme="light"] ::-webkit-scrollbar-thumb {
    background-color: #c6c6c6;
    border-radius: 6px;
  }

html[data-theme="light"] ::-webkit-scrollbar-thumb:hover {
    background-color: #a0a0a0;
  }

/*  */

html[data-theme="light"] ::-webkit-scrollbar-track {
    border-radius: 6px;
  }

/*  */

html[data-theme="light"] ::-webkit-scrollbar-button {
    display: none;
  }

html[data-theme="light"] a {
    color: #333;
  }

/*  */

html[data-theme="light"] .message-error-tips a {
    color: #2563eb !important;
  }

.App {
  width: 100%;
  height: 100%;

  /*  */
  padding-top: env(safe-area-inset-top);
  padding-right: env(safe-area-inset-right);
  padding-bottom: env(safe-area-inset-bottom);
  padding-left: env(safe-area-inset-left);
  padding-top: var(--mobile-safe-area-inset-top, 0px);
  padding-right: var(--mobile-safe-area-inset-right, 0px);
  padding-bottom: var(--mobile-safe-area-inset-bottom, 0px);
  padding-left: var(--mobile-safe-area-inset-left, 0px);
}

.ToolBar {
  /*  */
  padding-top: env(safe-area-inset-top, 0px);
  padding-bottom: env(safe-area-inset-bottom, 0px);
  padding-left: env(safe-area-inset-left, 0px);
  padding-top: var(--mobile-safe-area-inset-top, 0px);
  padding-bottom: var(--mobile-safe-area-inset-bottom, 0px);
  padding-left: var(--mobile-safe-area-inset-left, 0px);
}

.ThreadHistoryDrawer {
  /*  */
  padding-top: env(safe-area-inset-top);
  padding-right: env(safe-area-inset-right);
  padding-bottom: env(safe-area-inset-bottom);
  /* padding-left: env(safe-area-inset-left); */
  padding-top: var(--mobile-safe-area-inset-top, 0px);
  padding-right: var(--mobile-safe-area-inset-right, 0px);
  padding-bottom: var(--mobile-safe-area-inset-bottom, 0px);
  /* padding-left: var(--mobile-safe-area-inset-left, 0px); */
}

.Snackbar {
  /*  */
  padding-top: env(safe-area-inset-top);
  padding-right: env(safe-area-inset-right);
  padding-bottom: env(safe-area-inset-bottom);
  padding-left: env(safe-area-inset-left);
  padding-top: var(--mobile-safe-area-inset-top, 0px);
  padding-right: var(--mobile-safe-area-inset-right, 0px);
  padding-bottom: var(--mobile-safe-area-inset-bottom, 0px);
  padding-left: var(--mobile-safe-area-inset-left, 0px);
}

@font-face {
  font-family: "Cairo";
  src: url("./fonts/Cairo-Regular.ttf") format("truetype");
  font-weight: 400;
  font-style: normal;
}

@font-face {
  font-family: "Cairo";
  src: url("./fonts/Cairo-Bold.ttf") format("truetype");
  font-weight: 700;
  font-style: normal;
}
